html {
  font-size: 62.5%;
}

body {
  margin: 0;
  line-height: 2.2rem;
  letter-spacing: 0.2px;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
